<template>
  <breadcrumb-list :items="breadcrumbs"></breadcrumb-list>

  <div class="grid grid-cols-12 gap-4 sm:gap-5 lg:gap-6">
    <div class="col-span-12 xl:col-span-8">
      <div class="p-4 card sm:p-5">
        <form
          @input="form.clearError($event.target.name)"
          @submit.prevent="submit()"
        >
          <div class="space-y-4">
            <div>
              <label for="textName">{{ $t("attributes.name") }}</label>
              <span class="relative flex mt-1.5">
                <input
                  v-model="form.name"
                  id="textName"
                  name="name"
                  type="text"
                  class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 pl-9 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                />
                <span
                  class="pointer-events-none absolute flex h-full w-10 items-center justify-center text-slate-400 peer-focus:text-primary dark:peer-focus:text-accent dark:text-navy-300"
                >
                  <v-icon name="user-group" size="6"></v-icon>
                </span>
              </span>

              <validation-feedback>
                {{ form.getError("name") }}
              </validation-feedback>
            </div>

            <div>
              <label for="textSurveySize">{{
                $t("attributes.survey_size")
              }}</label>
              <span class="relative flex mt-1.5">
                <input
                  v-model="surveySize"
                  id="textSurveySize"
                  name="surveySize"
                  type="number"
                  max="100"
                  class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 pl-9 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                  @change="changeSize"
                />
                <span
                  class="pointer-events-none absolute flex h-full w-10 items-center justify-center text-slate-400 peer-focus:text-primary dark:peer-focus:text-accent dark:text-navy-300"
                >
                  <v-icon name="chevron-up-down" size="6"></v-icon>
                </span>
              </span>
            </div>

            <div
              class="rounded-lg border border-slate-200 p-4 dark:border-navy-600"
            >
              <div class="space-y-4">
                <div
                  class="sticky grid grid-cols-3 gap-x-2 gap-y-4 bg-white py-4 top-[61px] z-1"
                >
                  <div class="col-span-1 font-medium">
                    <span class="inline-block w-10">#</span>
                    <span>{{ $t("attributes.survey") }}</span>
                  </div>
                  <div class="col-span-1 font-medium">
                    {{ $t("attributes.template") }}
                  </div>
                  <div class="col-span-1 font-medium">
                    {{ $t("attributes.survey_at") }}
                  </div>
                </div>
                <div
                  v-for="(survey, index) in form.surveys"
                  :key="index"
                  class="grid grid-cols-3 gap-x-2 gap-y-4"
                >
                  <div class="col-span-1 flex">
                    <span class="w-10 self-center font-medium"
                      >{{ index + 1 }}.</span
                    >
                    <input
                      v-model="survey.name"
                      :name="`survey${index}Name`"
                      :id="`textSurvey${index}Name`"
                      type="text"
                      class="h-10 w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                    />
                  </div>
                  <div class="col-span-1">
                    <vue-select
                      :options="templates"
                      label="name"
                      :reduce="(template) => template.id"
                      v-model="survey.template_id"
                      :name="`survey${index}TemplateId`"
                    ></vue-select>
                  </div>
                  <div class="col-span-1">
                    {{}}
                    <input
                      :name="`survey${index}SurveyAt`"
                      :id="`dateSurvey${index}SurveyAt`"
                      :min="$moment().add(1, 'day').format('YYYY-MM-DD')"
                      :value="$moment(survey.survey_at).format('YYYY-MM-DD')"
                      type="date"
                      class="h-10 w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                      @input="survey.survey_at = $event.target.value"
                    />
                  </div>
                </div>
              </div>
            </div>

            <div class="text-end space-x-3">
              <v-button
                color="warning"
                @click="resetSurveys"
                :title="$t('placeholder.reset_surveys')"
              >
                {{ $t("labels.reset") }}
              </v-button>
              <v-button
                type="submit"
                color="primary"
                :disabled="!form.isSubmittable()"
              >
                {{ $t("labels.submit") }}
              </v-button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/core/Api";
import Form from "@/core/Form";
import Toast from "@/core/Toast";

const surveyObject = { name: "", template_id: "", survey_at: "" };

export default {
  name: "GroupSurveysForm",
  props: {
    id: {
      type: String,
      required: false,
    },
  },
  data() {
    return {
      breadcrumbs: [
        { title: this.$t("titles.groups"), route: "groups" },
        {
          title: this.$t("titles.surveys"),
          active: true,
        },
      ],
      surveySize: 1,
      templates: [],
      form: new Form({
        name: "",
        surveys: [],
      }),
    };
  },
  async created() {
    await api.get(`/templates/all`).then(({ data }) => {
      this.templates = data;
    });

    if (this.id) {
      await api.get(`/group/${this.id}`).then(({ data }) => {
        this.form.updateData(data);

        this.surveySize =
          data.surveys && Array.isArray(data.surveys) ? data.surveys.length : 1;

        if (
          !data.surveys ||
          (data.surveys && Array.isArray(data.surveys) && !data.surveys.length)
        ) {
          const surveysArray = Array(this.surveySize)
            .fill()
            .map(() => {
              return { ...surveyObject };
            });

          this.form.addData("surveys", surveysArray);
        }
      });
    }
  },
  methods: {
    changeSize() {
      const surveyArray = Array(this.surveySize)
        .fill()
        .map((item, index) => {
          if (this.form.surveys[index]) {
            return this.form.surveys[index];
          }

          return { ...surveyObject };
        });

      this.form.addData("surveys", surveyArray);
    },
    resetSurveys() {
      this.form.reset();
      this.changeSize();
    },
    submit() {
      this.form
        .json()
        .post("/group/update/surveys", this.form.getId())
        .then(() => {
          Toast.fire(this.$t("response.success.update"), "success");
        })
        .catch(() => {
          Toast.fire(this.form.getErrorMessage(), "error");
        });
    },
  },
};
</script>
